import { Controller, Block, Inline, Node } from '@ali/4ever-cangjie';
import { Heading } from '@ali/4ever-plugin-heading';
import { Image } from '@ali/4ever-plugin-image';
import { Paragraph } from '@ali/4ever-plugin-paragraph';

function isContainInlineNode(node: Block) {
  return node.nodes.some((n) => {
    if (Inline.isInline(n) && !Image.isImage(n)) {
      return true;
    } else if ((n as Block).type === 'line') {
      return isContainInlineNode(n as Block);
    }
    return false;
  });
}

/**
 * 判断 node 是否可拖拽
 * @param node
 * @param container
 */
export default function isNodeDraggable(controller: Controller, node: Node) {
  if (Inline.isInline(node)) return true;

  // 除文字段落外都可以拖
  if (!Heading.isHeading(node) && !Paragraph.isParagraph(node)) return true;

  if (isContainInlineNode(node)) {
    // 包含行内节点允许拖拽（类似公式、日期等）
    return true;
  }

  // PERF: node.text 迭代获取文本更费性能，这里尽量通过直接根据 nodes 判断
  const { nodes } = node;
  const firstNodeText = nodes[0].text;
  if (nodes.length === 1) {
    return !!firstNodeText;
  }

  if (nodes.length === 3) {
    const thirdNodeText = nodes[2].text;
    if (!firstNodeText && Image.isImage(nodes[1]) && !thirdNodeText) {
      // 根据特定结构判断，若是单张图片不允许拖拽
      return false;
    }
  }

  return true;
}
